.navbar {
    position: fixed;
    margin-top: 0;
    margin-left: 0;
    width: 100%;
    height: 100px;
    background-color: rgba(255, 255, 255, 0.655);
    display: flex;
    justify-content: flex-end;
    box-shadow: -10px 0 20px rgba(0, 0, 0, 1);
}
.links {
    display: flex;
    gap: 30px;
}
.links button {
    font-size: large;
    color: rgb(165, 134, 42);
    border: none;
    background: none;
}
.links button:hover {
    color: rgb(198, 156, 32);
}